.product a {
  position: relative;
  display: block;
  width: 350px;
  height: 650px;
  color: $color-default-white;

  background-color: $color-prussian-blue;
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: cover;

  &::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    background: $color-catalog-item-shadow;
    opacity: 0.5;
    transition: opacity $trans-default;
  }

  @include vp-1024 {
    width: 670px;
    height: 250px;
    background-size: cover;
  }

  @include vp-768 {
    width: 270px;
  }
}

// Фоновое изображение для каждой карточки в каталоге
.product--1 a {
  background-image: url("../img/catalog/card-1.jpg");

  @include retina {
    background-image: url("../img/catalog/card-1@2x.jpg");
  }
}

.product--2 a {
  background-image: url("../img/catalog/card-2.jpg");

  @include retina {
    background-image: url("../img/catalog/card-2@2x.jpg");
  }
}

.product--3 a {
  background-image: url("../img/catalog/card-3.jpg");

  @include retina {
    background-image: url("../img/catalog/card-3@2x.jpg");
  }
}

.product h3 {
  font-family: $ff-placeholder-second;
  font-size: 20px;
  line-height: 30px;
  position: absolute;
  max-width: 175px;
  margin: 0;
  top: 49px;
  left: 40px;

  @include vp-1024 {
    max-width: 150px;
    top: 36px;
    left: 45px;
  }

  @include vp-768 {
    max-width: 139px;
    font-size: 18px;
    top: 26px;
    left: 29px;
  }
}

.product span {
  position: absolute;
  content: "";
  width: 60px;
  height: 60px;
  background-color: $color-deep-sky-blue;
  border-radius: 3px 0 0 0;
  bottom: 0;
  right: 0;
  transition: width $trans-600;

  // Позиционирование стрелки
  display: flex;
  align-items: center;
  justify-content: center;

  @include vp-768 {
    width: 40px;
    height: 40px;
  }
}

/* Эффект при наведении на карточку товара */
.product a:hover::before {
  opacity: 0;
}

.product a:hover .product__icon {
  width: 100%;
}
